import React from 'react';
import Link from '../link/link';
import getClasses from '../../utils/getClasses';

const block = `features`;

const tabs = [
  {
    mod: `credits`,
    tabContent: ``,
    content: (
      <React.Fragment>
        <p>   </p>
        <Link
          draggable={false}
          href="#calculator"
          classes={`${getClasses({
            block: `button`,
            modifiers: [`main`],
          })} ${getClasses({
            block,
            element: `link`,
            modifiers: [`light`, `credits`],
          })}`}
        >
           
        </Link>
      </React.Fragment>
    ),
  },
  {
    mod: `confidence`,
    tabContent: ``,
    content: (
      <React.Fragment>
        <p>    </p>
      </React.Fragment>
    ),
  },
  {
    mod: `near`,
    tabContent: ``,
    tabMod: [`dark`],
    content: (
      <React.Fragment>
        <p> </p>
        <Link
          draggable={false}
          href="#departments"
          classes={`${getClasses({
            block: `button`,
            modifiers: [`main`],
          })} ${getClasses({block, element: `link`, modifiers: [`near`]})}`}
        >
           
        </Link>
      </React.Fragment>
    ),
  },
];

export {block};
export default tabs;
